.App {
  text-align: center;
}

#repo-list {
  list-style-type: none;
}

#search-bar, #submit-btn {
  padding: 5px;
  margin-top: 2%;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}

#search-bar {
  margin-right: 5px;
  border: 2px solid #333;
}

#submit-btn {
  border: none;
  background-color: #8cff00;
  color: #333;
  font-weight: 600;
  padding: 0.5em;
  transition: 0.3s;
}

#submit-btn:hover {
  background-color: #333;
  color: #8cff00;
}

#profile-item {
  display: grid;
  grid-template-columns: 1fr 2fr;
  text-align: left;
  padding: 0 0.5%;
  border: 2px solid #282c34;
  margin: 3% 20%;
  border-radius: 3%;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}

a {
  text-decoration: none;
  color: #333;
  font-weight: 800;
}

a:visited {
  color: #333;
}

#avatar-img img{
  margin-top: 2%;
  width: 30%;
  border-radius: 5px;
}

#user-page-wrapper {
  display: grid;
  grid-template-columns: 1fr 3fr;
}

#user-page-repos {
  margin-right: 2%;
}

#user-page-image img{
  border-radius: 6px;
  margin-top: 5%;
  width: 90%;
  height: 15%;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}

#user-page-image div {
  margin: 5% 0;
}

#repo-list {
  border-bottom: 1px solid #e1e4e8;
  padding-bottom: 10px;
  text-align: left;
  line-height: 1.6em;
}

#repo-name {
  font-size: 1.2em;
  font-weight: 800;
  color: #3ffc14;
}

#repo-desc {
  font-style: italic;
}

#repo-lang {
  color: #005c75;
  font-size: 0.9em;
}

#login-name {
  display: grid;
  grid-template-rows: 2fr 1fr;
}

.fa-star {
  color: gold;
}

#main-nav {
  background-color: #333;
  color: #8cff00;
  padding: 2%;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

#main-nav>div {
  font-size: 1.7em;
}

#score {
  color: steelblue;
}